import React, { useState, useEffect, useRef } from 'react'
import phonebookService from './services/people'
import Person from './components/Person'
import Notification from './components/Notification'
import './App.css'

const App = () => {
  const [persons, setPersons] = useState([])
  const [searchResults, setSearchResults] = useState([])
  const [searchTerm, setSearchTerm] = useState('')
  const [newName, setNewName] = useState('')
  const [newNumber, setNewNumber] = useState('')
  const [message, setMessage] = useState(null)

  useEffect(() => {
    phonebookService.getAll().then((initialPeople) => {
      setPersons(initialPeople)
      setSearchResults(initialPeople)
    })
  }, [])

  const reset = (e) => {
    setSearchTerm('')
    setSearchResults(persons)
  }

  const inputRef = useRef()

  useEffect((isFocused) => {
    if (isFocused) {
      inputRef.current.focus()
      inputRef.current.select()
    }
  }, [])

  useEffect(() => {
    function keyListener(e) {
      if (e.key === 'Escape') {
        setSearchTerm('')
        setSearchResults(persons)
      }
    }

    document.addEventListener('keydown', keyListener)

    return () => document.removeEventListener('keydown', keyListener)
  })

  const handleMessage = (data) => {
    setMessage(data)
    console.log(data)
    setTimeout(() => {
      setMessage(null)
    }, 3000)
  }

  const selectPerson = persons.filter((person) => {
    return person.name.toLowerCase() === newName.toLowerCase()
  })

  const addPerson = (event, name) => {
    event.preventDefault()
    const person = selectPerson[0]
    const personObject = {
      name: newName,
      number: newNumber,
      id: Math.floor(Math.random() * 100),
    }

    if (selectPerson.length === 0) {
      phonebookService
        .create(personObject)
        .then((returned) => {
          setPersons(persons.concat(returned))
          setSearchResults(persons.concat(returned))
          handleMessage({ text: `Added ${returned.name}`, status: 'ok' })
          setNewName('')
          setNewNumber('')
        })
        .catch((error) => {
          console.log(error.response.data.error)
          handleMessage({text: error.response.data.error})
        })

      return
    }
    /* updatePerson */
    const changedNumber = { ...person, number: newNumber }

    if (selectPerson.length > 0) {
      const conf = window.confirm(`Change number for ${person.name}?`)
      if (conf === true) {
        phonebookService
          .update(person.id, changedNumber)
          .then((returned) => {
            setSearchResults(
              searchResults.map((p) =>
                p.name !== person.name ? p : changedNumber
              )
            )
            handleMessage({
              text: `Changed number for ${returned.name} to ${returned.number} `,
              status: 'ok',
            })
            setNewName('')
            setNewNumber('')
          })
          .catch((error) => {
            handleMessage({ text: String(error), status: 'error' })
          })
      }
      setNewName('')
      setNewNumber('')
      return
    }
  }

  const deletePerson = (person) => {
    let index = searchResults.indexOf(person)
    const conf = window.confirm(
      `Are you sure you want to delete ${person.name}?`
    )
    if (conf === true) {
      phonebookService
        .deletePerson(person)
        .then(() =>
          handleMessage({ text: `${person.name} deleted `, status: 'ok' })
        )
      searchResults.splice(index, 1)
      setSearchResults([...searchResults])
      return
    }
    return
  }

  const handleNameChange = (event) => {
    setNewName(event.target.value)
  }

  const handleNumberChange = (event) => {
    setNewNumber(event.target.value)
  }

  const handleSearchChange = (e) => {
    setSearchTerm(e.target.value)
  }

  const searchPerson = persons.filter((person) => {
    return person.name.toLowerCase().startsWith(searchTerm.toLowerCase())
  })

  const handleSearch = (e) => {
    e.preventDefault()
    setSearchResults(searchPerson)
    inputRef.current.select()
  }

  return (
    <div>
      <h1>Phonebook</h1>
      <Notification message={message} />
      <form onSubmit={handleSearch}>
        Search:{' '}
        <input
          value={searchTerm}
          onChange={handleSearchChange}
          ref={inputRef}
        />
        <button type="submit">Search</button>
        <button onClick={reset}>Reset</button>
      </form>
      <ul>
        {searchResults.map((person, i) => (
          <Person
            key={i}
            person={person}
            value={person.id}
            deletePerson={deletePerson}
          />
        ))}
      </ul>
      <h1>Add Person:</h1>
      <form onSubmit={addPerson}>
        Name: <input value={newName} onChange={handleNameChange} />
        Number: <input value={newNumber} onChange={handleNumberChange} />
        <button type="submit">Save</button>
      </form>
    </div>
  )
}

export default App
