import React from 'react'

const Person = ({ person, deletePerson }) => {
  return (
    <>
      <li key={person.id}>
        {person.name}: {person.number}
      </li>
      <button value={person} onClick={() => deletePerson(person)}>
        Delete {person.name}
      </button>
    </>
  )
}

export default Person
